<template>
  <div class="task">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>任务管理</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search">
      <el-form label-position="top"
               label-width="80px"
               :model="search">
        <el-form-item label="外贸合同号">
          <el-input v-model="search.purchaseContractNo"
                    placeholder="请输入或选择"
                    clearable></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="search.status"
                     placeholder="请输入或选择">
            <el-option label="未完成"
                       value="1"></el-option>
            <el-option label="已完成"
                       value="2"></el-option>
            <el-option label="全部"
                       value=""></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-button type="primary"
                 @click="query">查询</el-button>
    </div>
    <div class="table">
      <el-table :data="tableData"
                style="width: 100%">
        <el-table-column prop="purchaseContractNo"
                         label="外贸合同号"
                         width="100">
        </el-table-column>
        <el-table-column prop="category"
                         label="油种"
                         width="100">
        </el-table-column>
        <el-table-column prop="specification"
                         label="规格">
        </el-table-column>
        <el-table-column prop="quantity"
                         label="数量">
        </el-table-column>
        <el-table-column prop="laycan"
                         label="装期">
        </el-table-column>
        <el-table-column prop="forwarderStatus"
                         label="状态">
          <template slot-scope="scope">
            <el-tag :type="scope.row.forwarderStatus === '未完成' ? 'primary' : 'success'">{{scope.row.forwarderStatus}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="nomineeCompany"
                         label="委托公司">
        </el-table-column>
        <el-table-column fixed="right"
                         label="操作"
                         width="60">
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)"
                       type="text"
                       size="small">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-pagination layout="prev, pager, next"
                   @current-change="handleCurrentChange"
                   :total="search.totalNum">
    </el-pagination>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'task',
  data () {
    return {
      search: {
        purchaseContractNo: '',
        status: '',
        pageNum: 1,
        totalNum: 0
      },
      tableData: []
    }
  },
  created () {
    // this.login()
    this.query()
  },
  methods: {
    // 查询条件
    query () {
      this.ajax(
        url.task,
        this.search,
        'post'
      ).then(res => {
        // console.log(res.data.data)
        if (res.data.status === 0) {
          this.tableData = res.data.data
          this.search.totalNum = res.data.total
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // 跳转路由
    handleClick: function (e) {
      // console.log(e.forwarderStatus)
      this.$router.push({ path: '/taskDetail', query: { dealUuid: e.dealUuid, status: e.forwarderStatus } })
    },
    // 分页
    handleCurrentChange: function (e) {
      console.log(e)
      this.search.pageNum = e
      this.query()
    }
  }
}
</script>

<style lang="scss"  scoped>
@mixin form {
  float: left;
  padding-right: 15px;
}
.task {
  background: rgba(255, 255, 255, 0);
}
.el-form-item:first-child {
  width: 365px;
  @include form;
}
.el-form-item:last-child {
  width: 168px;
  @include form;
}
.el-form--label-top .el-form-item__label {
  font-size: 12px;
  color: rgba(158, 160, 165, 1);
}
.el-button--primary {
  margin-top: 50px;
}
.el-table_1_column_1 {
  color: #9ea0a5;
}
.el-tag--primary {
  background: rgba(246, 171, 47, 1);
  color: #fff;
}
.el-tag--success {
  background: rgba(52, 170, 68, 1);
  color: #fff;
}
</style>



