import Vue from 'vue'
import Router from 'vue-router'
import task from '@/components/task.vue'
import taskDetail from '@/components/taskDetail.vue'
import order from '@/components/order.vue'
import sof from '@/components/sof.vue'
import lading from '@/components/lading.vue'
import details from '@/components/details.vue'
import upload from '@/components/upload.vue'
import printSof from '@/components/printSof.vue'
import printBl from '@/components/printBl.vue'
Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [{
    path: '/',
    redirect: '/task'
  },
  {
    path: '/task',
    name: 'task',
    component: task
  },
  {
    path: '/taskDetail',
    name: 'taskDetail',
    component: taskDetail
  },
  {
    path: '/order',
    name: 'order',
    component: order
  },
  {
    path: '/sof',
    name: 'sof',
    component: sof
  },
  {
    path: '/lading',
    name: 'lading',
    component: lading
  },
  {
    path: '/details',
    name: 'details',
    component: details
  },
  {
    path: '/upload',
    name: 'upload',
    component: upload
  },
  {
    path: '/printSof',
    name: 'printSof',
    component: printSof
  },
  {
    path: '/printBl',
    name: 'printBl',
    component: printBl
  }
  ]
})



