<template>
  <div class="sidebar">
    <div class="logo">
      <img :src="imgUrl"
           alt="">
      <p>{{companyName}}</p>
    </div>
    <div class="nav">
      <el-menu :default-active="activeItem"
               @select='activeIcon'>
        <el-menu-item-group v-for="(item, key) in menuList"
                            :key='key'>
          <template slot="title">{{item.title}}</template>
          <el-menu-item v-for="(gitem, gkey) in item.children"
                        :key='gkey'
                        :index="gitem.index">
            <i class="iconfont"
               :class="'icon-' + gitem.icon"></i>
            <span>{{gitem.title}}</span>
          </el-menu-item>
        </el-menu-item-group>
      </el-menu>
    </div>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'sidebar',
  data () {
    return {
      activeItem: '',
      imgUrl: '',
      companyName: ' ',
      menuList: [
        {
          title: '任务管理',
          children: [
            {
              title: '任务管理',
              index: '/task',
              icon: 'list'
            }
          ]
        },
        {
          title: '单证管理',
          children: [
            {
              title: '我的单证',
              index: '/order',
              icon: 'doc'
            }
          ]
        },
        {
          title: '个人中心',
          children: [
            {
              title: '账户信息',
              index: '/3-1',
              icon: 'zhanghuxinxi'
            },
            {
              title: '账号安全',
              index: '/3-2',
              icon: 'zhanghuanquan'

            },
            {
              title: '修改密码',
              index: '/3-3',
              icon: 'pwd'
            },
            {
              title: '子账号管理',
              index: '/3-4',
              icon: 'branch'
            }
          ]
        }
      ]
    }
  },
  computed: {
  },
  watch: {
    $route (val) {
      this.activeItem = val.fullPath
    }
  },
  created () {
    this.userInfor()
    this.activeItem = this.$route.fullPath
  },
  methods: {
    activeIcon (e) {
      this.$router.push({ path: e, query: {} })
    },
    userInfor () {
      this.ajax(
        url.userInfor,
        {},
        'get'
      ).then(res => {
        this.companyName = res.data.enterpriseName
        this.imgUrl = res.data.webDomain + res.data.loginLogo
        // console.log(this.imgUrl)
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@mixin nav {
  width: 278px;
  height: 46px;
  line-height: 46px;
}
@mixin active {
  background: rgba(246, 249, 253, 1);
  border-left: 3px solid #1665d8;
  color: #1665d8;
}
.sidebar {
  .logo {
    text-align: center;
    margin: 30px;
    border-bottom: 1px solid #eaedf3;
    img {
      width: 56px;
      height: 56px;
    }
    p {
      padding: 10px 0 30px;
    }
  }
  .iconfont {
    margin-right: 20px;
  }
}
.el-menu-item.is-active {
  @include active;
  @include nav;
}
.el-menu-item {
  border-left: 3px solid #ffffff;
  @include nav;
}
.el-menu-item:hover {
  @include active;
  @include nav;
}
</style>



