<template>
  <div class="details">
    <div class="tab">
      <span @click="back()">任务详情</span>>
      <span>单证详情</span>
    </div>
    <div class="file">
      <h3 class="clearfix">
        <em class="fl">附件</em>
        <router-link :to="{path:'/upload',query: {blId: this.$route.query.blId,sofId:this.$route.query.sofId,taskId:this.$route.query.id}}">
          <span class="fr">
            <i class="iconfont icon-list"></i> 上传单证</span>
        </router-link>
      </h3>
      <el-table :data="fileList"
                style="width:100%">
        <el-table-column prop="name"
                         label="单证名称"
                         width="400">
        </el-table-column>
        <el-table-column prop="createDate"
                         label="上传时间"
                         width="150">
        </el-table-column>
        <el-table-column prop="fileType"
                         label="单证类型"
                         width="150">
        </el-table-column>
        <el-table-column label="操作"
                         width="178">
          <template slot-scope="scope">
            <el-button type="text"
                       size="small"
                       @click="handleClick(scope.row)">查看</el-button>
            <el-button @click.native.prevent="deleteRow(scope.$index, fileList)"
                       type="text"
                       size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-tabs v-model="activeName"
             @tab-click="tab">
      <el-tab-pane label="SOF"
                   name="first">
        <div class="tag clearfix">
          <router-link :to="{path:'/sof',query: {sofId: this.$route.query.sofId,blId: this.$route.query.blId,id:this.$route.query.id}}">
            <div>
              <el-tag>
                <i class="iconfont icon-security"></i> 修改</el-tag>
            </div>
          </router-link>
          <router-link target="_blank"
                       :to="{path:'/printSof',query: {sofId: this.$route.query.sofId,id:this.$route.query.id,print:1}}">
            <div>
              <el-tag>
                <i class="iconfont icon-security"></i> 打印单证</el-tag>
            </div>
          </router-link>
        </div>
        <div class="info">
          <div class="clearfix con">
            <img :src="imgUrl"
                 alt=""
                 class="fl">
            <div class="fl">
              <!-- <h3>{{fullName}}</h3>
              <p>{{englishName}}</p> -->
              <h3>泉州市泉港兴通船务有限公司</h3>
              <p>Quanzhou Quangang Xingtong Shipping Co.,Ltd</p>
            </div>
          </div>
          <h4>装货时间事实记录 LAYTIME STATEMENT OF FACTS</h4>
          <ul class="clearfix">
            <li>
              <em>船名 M/V:</em>
              <span>{{vessel}}</span>
            </li>
            <li>
              <em>航次 VOY.NO.:</em>
              <span>{{voyage}}</span>
            </li>
            <li>
              <em>装货名称 Loading:</em>
              <span>{{goodsName}}</span>
            </li>
            <li>
              <em>日期 DATE:</em>
              <span>{{sofDate}}</span>
            </li>
            <li>
              <em>吨数 MT:</em>
              <span>{{metricTon}}</span>
            </li>
            <li>
              <em>港口 LOADING PORT:</em>
              <span>{{port}}</span>
            </li>
          </ul>
        </div>
        <div class="form clearfix">
          <table border="1px"
                 cellspacing="0px"
                 style="border-collapse:collapse"
                 class="table">
            <tr>
              <th width="15%">日期</th>
              <th width="15%">星期</th>
              <th width="15%">开始时间</th>
              <th width="15%">结束时间</th>
              <th width="40%">说明</th>
            </tr>
            <tr v-for="(item, key) in tableData"
                :key='key'>
              <td v-show="item.detailDate">
                {{timestampToTime(item.detailDate)}}
              </td>
              <td v-show="item.detailDate">
                {{format(new Date(item.detailDate).getDay())}}
              </td>
              <td v-show="!item.detailDate"></td>
              <td>
                {{item.beginTime}}
              </td>
              <td>
                {{item.endTime}}
              </td>
              <td>
                {{item.instruction}}
              </td>
            </tr>
          </table>
        </div>
        <ul class="remark clearfix">
          <li style="width:100%">
            <em>备注 REMARK:</em>
            <span>{{remark}}</span>
          </li>
          <li>
            <em>船舶登录总吨 SHIP'S.G.R.T:</em>
            <span>{{shipGrossWeight}}</span>
          </li>
          <li>
            <em>船舶净吨 SHIP'S.N.R.T:</em>
            <span>{{shipNetWeight}}</span>
          </li>
          <li>
            <em>船长 CAPTAIN:</em>
            <span></span>
          </li>
          <li>
            <em>代理公司 AS AGENT:</em>
            <span></span>
          </li>
        </ul>
      </el-tab-pane>
      <el-tab-pane label="提单"
                   name="second">
        <div class="tag clearfix">
          <router-link :to="{path:'/lading',query: {sofId:this.$route.query.sofId,blId:this.$route.query.blId,id:this.$route.query.id}}">
            <div>
              <el-tag>
                <i class="iconfont icon-security"></i> 修改</el-tag>
            </div>
          </router-link>
          <router-link target="_blank"
                       :to="{path:'/printBl',query: {blId: this.$route.query.blId,id:this.$route.query.id,print:1}}">
            <div>
              <el-tag>
                <i class="iconfont icon-security"></i> 打印单证</el-tag>
            </div>
          </router-link>
        </div>
        <table border="1px"
               cellspacing="0px"
               style="table-layout:fixed;border-collapse:collapse;"
               class="table1">
          <tr>
            <th colspan="8">
              <em class="fr">*B/L NO:{{ruleForm.blNo}}</em>TANKER BILL OF LADING
            </th>
          </tr>
          <tr>
            <td colspan="3"
                class="bg">*Shipped on board in apparent good order and condition by (Consignor)</td>
            <td colspan="5">
              <p>{{ruleForm.consignor}}</p>
            </td>
          </tr>
          <tr>
            <td class="bg">*on board the tanker</td>
            <td colspan="2">
              <p>{{ruleForm.vessel}}</p>
            </td>
            <td class="bg"
                colspan="2">*Shipped on board</td>
            <td colspan="3">
              <p>{{ruleForm.loadingPort}}</p>
            </td>
          </tr>
          <tr>
            <td class="bg">*Where of</td>
            <td colspan="2">
              <p>{{ruleForm.unloadingPort}}</p>
            </td>
            <td class="bg"
                colspan="2">*Is the Master, to be delivered to the port of discharge</td>
            <td colspan="3">
              <p>{{ruleForm.captain}}</p>
            </td>
          </tr>
          <tr>
            <td class="bg"
                colspan="2">*Consignee</td>
            <td colspan="6">
              <p>{{ruleForm.consignee}}</p>
            </td>
          </tr>
          <tr>
            <td class="bg"
                colspan="2">*Notify</td>
            <td colspan="6">
              <p>{{ruleForm.notify}}</p>
            </td>
          </tr>
          <tr>
            <td colspan="3"
                rowspan="6">
              <p>{{ruleForm.category}}</p>
            </td>
            <td colspan="5"
                class="bg"
                align="center">QUANTITY</td>
          </tr>
          <tr>
            <td colspan="3">
              <p>{{ruleForm.metricTon}}</p>
            </td>
            <td class="bg"
                colspan="2">*MT</td>
          </tr>
          <tr>
            <td colspan="3">
              <p>{{ruleForm.longTon}}</p>
            </td>
            <td class="bg"
                colspan="2">*LT</td>
          </tr>
          <tr>
            <td colspan="3">
              <p>{{ruleForm.bbls}}</p>
            </td>
            <td class="bg"
                colspan="2">*US BBLS 60F</td>
          </tr>
          <tr>
            <td colspan="3">
              <p>{{ruleForm.ltrs}}</p>
            </td>
            <td class="bg"
                colspan="2">*LTRS AT 15C</td>
          </tr>
          <tr>
            <td colspan="3">
              <p>{{ruleForm.ltrsObsTrmp}}</p>
            </td>
            <td class="bg"
                colspan="2">*LTRS AT OBS TRMP</td>
          </tr>
          <tr>
            <td colspan="8">
              <p>{{ruleForm.remark}}</p>
            </td>
          </tr>
          <tr>
            <td colspan="8"
                class="pd20 color">The quantity, measurement, weight, gauge, quality nature and value and actual condition of the cargo unknown to the Vessel and the Master to be delivered at the port of discharge or so near thereto as the Vessel can safely get, always afloat upon prior payment of freight as agreed.</td>
          </tr>
          <tr>
            <td colspan="3"
                class="bg">*This shipment is carried under and pursuant to the terms of the Charter dated</td>
            <td colspan="5">
              <p>{{ruleForm.cpDate1}}</p>
            </td>
          </tr>
          <tr>
            <td class="bg"
                colspan="2">*at</td>
            <td colspan="2">
              <p>{{ruleForm.cpDate2}}</p>
            </td>
            <td class="bg"
                colspan="2">*Between</td>
            <td colspan="2">
              <p>{{ruleForm.cpDate3}}</p>
            </td>
          </tr>
          <tr>
            <td class="bg"
                colspan="2">*and</td>
            <td colspan="6">
              <p>{{ruleForm.cpDate4}}</p>
            </td>
          </tr>
          <tr>
            <td colspan="8"
                class="pd20 color">as Charterer, and all the terms whatsoever of the said Charter except the rate and payment of freight specified therein apply to and govern the rights of the parties concerned in this shipment. Copy of the Charter may be obtained from the Shipper or Charterer. If this Bill of Lading is a document of title to which the Carriage of Goods by Sea Act of the United Sates, approved April 16, 1936, or similar legislation giving statutory effect to the International convention for the Unification of Certain Rules relating to Bills of Lading at Brussels of August 25, 1924, applies by reason of the port of loading or discharge being in territory in which the said Act or other similar legislation is in force, this Bill of Lading shall have effect subject to the provisions of the said Act or other similar legislation, as the case may be, which shall be deemed incorporated herein, and nothing herein contained shall be deemed a surrender by the carrier of any of its rights or immunities or an increase of any of its responsibilities or liabilities under said Act or other similar legislation. If any term of this Bill of Lading is repugnant to the said Act or other similar legislation as so incorporated, such terms shall be void to that extent but no further. The contract of carriage evidenced by this Bill of Lading is between the shipper, consignee and/or owner of the cargo and the owner or demise chartered of the vessel named herein to carry the cargo described above. It is understood and agreed that, other than said shipowner or demise charterer, no person, firm or corporation or other legal entity whatsoever, is or shall be deemed to be liable with respect to the shipment as carrier, bailee or otherwise in contract or in tort. If, however, it shall be adjudged that any other than said shipowner or demise charterer is carrier or bailee of said shipment or under any responsibility with respect thereto, all limitations of or exoneration’s from liability and all defenses provided by law or by the terms of the contract of carriage shall be available to such other. </td>
          </tr>
          <tr>
            <td colspan="4"
                class="bg">*In witness where of, the Master has signed</td>
            <td colspan="4">
              <p>{{ruleForm.signNum}}</p>
            </td>
          </tr>
          <tr>
            <td colspan="8"
                class="pd20 color">Bills of Lading of this tenor and date, one of which being accomplished, the others will be void.</td>
          </tr>
          <tr>
            <td class="bg">*timeZone</td>
            <td colspan="1">
              <p>{{ruleForm.timeZone}}</p>
            </td>
            <td class="bg"
                colspan="1">*day of</td>
            <td colspan="1">
              <p>{{ruleForm.timeDay}}</p>
            </td>
            <td class="bg"
                colspan="1">*month of</td>
            <td colspan="1">
              <p>{{ruleForm.timeMonth}}</p>
            </td>
            <td class="bg"
                colspan="1">*year of</td>
            <td colspan="1">
              <p>{{ruleForm.timeYear}}</p>
            </td>
          </tr>
          <tr>
            <th colspan="8">
              <p class="fr">{{ruleForm.masterName}}</p>
              <em class="fr">*MASTER OF</em>
            </th>
          </tr>
        </table>

      </el-tab-pane>
    </el-tabs>

  </div>
</template>

<script>
import url from '@/api/api'
import img from '@/assets/logo.jpg'
import { formateDate } from '@/filters/index'
export default {
  name: 'detail',
  data () {
    return {
      imgUrl: img,
      englishName: '',
      fullName: '',
      vessel: '',
      voyage: '',
      goodsName: '',
      sofDate: '',
      metricTon: '',
      port: '',
      remark: '',
      shipGrossWeight: '',
      shipNetWeight: '',
      activeName: 'first',
      fileList: [],
      tableData: [{
        detailDate: '',
        week: '',
        beginTime: '',
        endTime: '',
        instruction: ''
      }],
      ruleForm: {
        taskId: this.$route.query.id,
        blNo: '',
        consignor: '',
        vessel: '',
        loadingPort: '',
        captain: '',
        unloadingPort: '',
        consignee: '',
        notify: '',
        category: '',
        metricTon: '',
        longTon: '',
        bbls: '',
        ltrs: '',
        ltrsObsTrmp: '',
        remark: '',
        cpDate1: '',
        cpDate2: '',
        cpDate3: '',
        cpDate4: '',
        signNum: '',
        timeZone: '',
        timeDay: '',
        timeMonth: '',
        timeYear: '',
        masterName: ''
      }
    }
  },
  watch: {
  },
  created () {
    this.flieList()
    if (this.$route.query.blId) {
      this.activeName = 'second'
      this.billDetail()
    }
    if (this.$route.query.sofId) {
      this.activeName = 'first'
      this.sofDetail()
    }
  },
  methods: {
    handleClick (e) {
      window.open(e.path)
    },
    format (e) {
      switch (e) {
        case 0:
          return 'Sun.'
        case 1:
          return 'Mon.'
        case 2:
          return 'Tues.'
        case 3:
          return 'Wed.'
        case 4:
          return 'Thur.'
        case 5:
          return 'Fri.'
        case 6:
          return 'Sat.'
        default:
          return ''
      }
    },
    timestampToTime (timestamp) {
      if (!timestamp) {
        return ''
      }
      var date = new Date(timestamp)
      var Y = date.getFullYear() + '-'
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      var D = date.getDate() + ' '
      return Y + M + D
    },
    back () {
      this.$router.go(-1)
    },
    deleteRow (index, rows) {
      let obj = {}
      obj.id = rows[index].id
      obj.fileType = rows[index].fileType === '提单' ? 'BL' : 'SOF'
      this.deleteFile(obj)
      this.flieList()
      // rows.splice(index, 1)
    },
    tab (tab, event) {
      // console.log(tab, event)
    },
    // sof详情
    sofDetail () {
      this.ajax(
        url.sofDetail,
        { id: this.$route.query.sofId },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.fullName = res.data.data.fullName
          this.englishName = res.data.data.fullName
          // this.imgUrl = res.data.data.logo
          this.tableData = res.data.data.detailList
          this.vessel = res.data.data.vessel
          this.voyage = res.data.data.voyage
          this.goodsName = res.data.data.goodsName
          this.sofDate = this.timestampToTime(res.data.data.sofDate)
          this.metricTon = res.data.data.metricTon
          this.port = res.data.data.port
          this.remark = res.data.data.remark
          this.shipGrossWeight = res.data.data.shipGrossWeight
          this.shipNetWeight = res.data.data.shipNetWeight
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // bill详情
    billDetail () {
      this.ajax(
        url.billDetail,
        { id: this.$route.query.blId },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.ruleForm = res.data.data
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // 附件列表
    flieList () {
      this.ajax(
        url.fileList,
        {
          blId: this.$route.query.blId,
          sofId: this.$route.query.sofId
        },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          let arr = []
          res.data.data.forEach((value, index, array) => {
            let obj = res.data.data[index]
            obj.name = value.name
            obj.createDate = formateDate(value.createDate)
            obj.fileType = (value.fileType === '1') ? 'SOF' : '提单'
            arr.push(obj)
          })
          this.fileList = arr
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // 删除
    deleteFile (data) {
      this.ajax(
        url.delete,
        data,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.flieList()
          this.$message(res.data.message)
        } else {
          this.$message(res.data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.remark {
  padding-bottom: 30px;
  li {
    float: left;
    width: 50%;
    padding: 10px 0;
    em {
      padding-right: 10px;
      color: #9ea0a5;
    }
  }
}
.details {
  .file {
    border: 1px solid rgba(234, 237, 243, 1);
    h3 {
      padding: 10px;
      border-bottom: 1px solid rgba(234, 237, 243, 1);
      em {
        line-height: 33px;
        color: #3e3f42;
        font-weight: bold;
      }
      span {
        color: #3e3f42;
        font-weight: normal;
        padding: 6px;
        background: linear-gradient(
          360deg,
          rgba(246, 247, 249, 1) 0%,
          rgba(255, 255, 255, 1) 100%
        );
        box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
          0px 2px 0px 0px rgba(255, 255, 255, 0.05);
        border-radius: 4px;
        border: 1px solid rgba(216, 220, 230, 1);
      }
    }
  }
  .tag {
    padding: 20px 0;
    background: #fbfbfd;
    div {
      float: left;
    }
    .el-tag {
      box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
        0px 2px 0px 0px rgba(255, 255, 255, 0.05);
      border-radius: 4px;
      border: 1px solid rgba(216, 220, 230, 1);
      width: 104px;
      height: 44px;
      line-height: 44px;
      text-align: center;
      margin-right: 20px;
      color: rgba(62, 63, 66, 1);
      i {
        color: rgba(216, 220, 230, 1);
        padding-right: 6px;
      }
    }
    .el-tag:hover {
      background: linear-gradient(
        360deg,
        rgba(232, 233, 235, 1) 0%,
        rgba(239, 240, 242, 1) 100%
      );
    }
  }
  .info {
    .con {
      padding: 30px 0;
      img {
        width: 70px;
        padding-right: 30px;
      }
      h3 {
        height: 26px;
        font-size: 20px;
        color: rgba(51, 51, 51, 1);
        line-height: 26px;
        padding-bottom: 20px;
      }
      p {
        height: 19px;
        font-size: 14px;
        color: rgba(51, 51, 51, 1);
        line-height: 19px;
      }
    }
    h4 {
      height: 28px;
      font-size: 14px;
      font-weight: 500;
      color: rgba(62, 63, 66, 1);
      line-height: 28px;
    }
    li {
      float: left;
      width: 33%;
      padding: 10px 0;
      em {
        padding-right: 10px;
        color: #9ea0a5;
      }
    }
  }
  .form {
    border: 1px solid #eaedf3;
    padding: 20px;
    margin-bottom: 20px;
  }
  .table {
    text-align: center;
    margin: 20px 0;
    width: 100%;
    th {
      border: 1px solid #eaedf3;
      line-height: 40px;
      background: #f5f6f7;
    }
    td {
      border: 1px solid #eaedf3;
      line-height: 50px;
    }
  }
  .table1 {
    margin: 20px 0;
    width: 100%;
    font-size: 12px;
    th {
      border: 1px solid #eaedf3;
      text-align: left;
      line-height: 40px;
      padding: 20px 0 20px 20px;
      em {
        padding-right: 10px;
      }
    }
    td {
      border: 1px solid #eaedf3;
      width: 16%;
      line-height: 30px;
    }
  }
  .bg {
    background: #f5f6f7;
    padding: 4px;
    color: #333333;
  }
}
</style>



