<template>
  <div class="taskDetail">
    <div class="tab">
      <span @click="back()">任务管理</span>>
      <span>任务详情</span>
    </div>
    <div class="menu">
      <div class="fl">
        <i class="iconfont icon-doc"></i>
        <span>{{state}}</span>
      </div>
      <ul class="fr">
        <router-link :to="{path:'/sof',query: {id: Id,dealUuid:this.$route.query.dealUuid,status:this.$route.query.status}}"
                     v-show="!sofId">
          <li>新建SOF单证</li>
        </router-link>
        <router-link :to="{path:'/lading',query: {id: Id,dealUuid:this.$route.query.dealUuid,status:this.$route.query.status}}"
                     v-show="!blId">
          <li>新建提单</li>
        </router-link>
        <router-link :to="{path:'/details',query: {id: Id,sofId:sofId,blId:blId,dealUuid:this.$route.query.dealUuid,status:this.$route.query.status}}"
                     v-show="sofId">
          <li>查看SOF单证</li>
        </router-link>
        <router-link :to="{path:'/details',query: {id: Id,sofId:sofId,blId:blId,dealUuid:this.$route.query.dealUuid,status:this.$route.query.status}}"
                     v-show="blId">
          <li>查看提单</li>
        </router-link>
      </ul>
    </div>
    <div class="detail">
      <h3>Deal Recap</h3>
      <ul class="clearfix">
        <li>
          <em>Deal No :</em>
          <span>{{list.serialNumber}}</span>
        </li>
        <li>
          <em>Category :</em>
          <span>{{list.productCategory}}</span>
        </li>
        <li>
          <em>Quantity :</em>
          <span>{{list.quantity}}</span>
        </li>
        <li>
          <em>Incoterms :</em>
          <span>{{list.tradeTerm}}</span>
        </li>
        <li>
          <em>Tolerance :</em>
          <span>{{list.tolerance}}</span>
        </li>
        <li>
          <em>Quality Standard :</em>
          <a :href="list.qualityStandard"><span>{{list.qualityStandard}}</span></a>
        </li>
        <li>
          <em>Quantity DTER :</em>
          <span>{{list.quantityDter}}</span>
        </li>
      </ul>
    </div>
    <div class="detail">
      <h3>Transportation</h3>
      <ul class="clearfix">
        <li>
          <em>Transport Mode :</em>
          <span>{{list.transportMode}}</span>
        </li>
        <li>
          <em>Laycan :</em>
          <span>{{list.laycanDateAsString }}</span>
        </li>
        <li>
          <em>Laytime :</em>
          <span>{{list.laytimeAsString}} H+6</span>
        </li>
        <li>
          <em>Demurrage Rate :</em>
          <span>{{list.demurrageRateNum}}</span>
        </li>
      </ul>
    </div>
    <div class="detail">
      <h3>Seller</h3>
      <ul class="clearfix">
        <li>
          <em>Company :</em>
          <span>{{list.salerName}}</span>
        </li>
        <li>
          <em>Address :</em>
          <span>{{list.salerAddress}}</span>
        </li>
        <li>
          <em>Contact Person :</em>
          <span>{{list.salerTraderName}}</span>
        </li>
        <li>
          <em>Email :</em>
          <span>{{list.salerEmail}}</span>
        </li>
        <li>
          <em>Tel :</em>
          <span>{{list.salerTelephone}}</span>
        </li>
        <li>
          <em>Fax : </em>
          <span>{{list.salerFax}}</span>
        </li>
      </ul>
    </div>
    <div class="detail">
      <h3>Buyer</h3>
      <ul class="clearfix">
        <li>
          <em>Company :</em>
          <span>{{list.buyerName}}</span>
        </li>
        <li>
          <em>Address :</em>
          <span>{{list.buyerAddress}}</span>
        </li>
        <li>
          <em>Contact Person :</em>
          <span>{{list.buyerTraderName}}</span>
        </li>
        <li>
          <em>Email :</em>
          <span>{{list.buyerEmail}}</span>
        </li>
        <li>
          <em>tel :</em>
          <span>{{list.buyerTelephone}}</span>
        </li>
        <li>
          <em>Fax :</em>
          <span>{{list.buyerFax}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'taskDetail',
  data () {
    return {
      state: this.$route.query.status,
      blId: '',
      sofId: '',
      Id: '',
      list: {}
    }
  },
  created () {
    this.query()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    // 数据展示
    query () {
      this.ajax(
        url.taskDetail,
        {
          dealUuid: this.$route.query.dealUuid
        },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.list = res.data.data.detail
          this.sofId = res.data.data.sofId
          this.blId = res.data.data.blId
          this.Id = res.data.data.id
        } else {
          this.$message(res.data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss"  scoped>
.taskDetail {
  background: rgba(251, 251, 253, 1);
  .menu {
    height: 88px;
    line-height: 88px;
    background: #ffffff;
    border: 1px solid rgba(234, 237, 243, 1);
    padding: 0 20px;
    margin-bottom: 20px;
    span,
    i {
      font-size: 20px;
      font-weight: 500;
      color: rgba(62, 63, 66, 1);
      padding: 0 5px;
    }
    li {
      float: left;
      width: 100px;
      height: 38px;
      line-height: 38px;
      margin: 25px 5px;
      font-size: 14px;
      font-weight: 400;
      text-align: center;
      color: rgba(62, 63, 66, 1);
      background: linear-gradient(
        360deg,
        rgba(246, 247, 249, 1) 0%,
        rgba(255, 255, 255, 1) 100%
      );
      box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
        0px 2px 0px 0px rgba(255, 255, 255, 0.05);
      border-radius: 4px;
      border: 1px solid rgba(216, 220, 230, 1);
    }
  }
  .detail {
    padding: 20px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.04);
    border: 1px solid rgba(234, 237, 243, 1);
    h3 {
      font-size: 20px;
      padding-bottom: 10px;
    }
    li {
      float: left;
      width: 50%;
      padding: 10px 0;
      em {
        width: 40%;
        text-align: right;
        float: left;
        color: rgba(102, 102, 102, 1);
        line-height: 16px;
      }
      span {
        width: 60%;
        float: left;
        color: rgba(51, 51, 51, 1);
        line-height: 16px;
      }
    }
  }
}
</style>



