<template>
  <div id="app">
    <div v-show='!this.$route.query.print'>
      <headBar />
      <div class="content clearfix">
        <div class="fl sidebar">
          <sidebar />
        </div>
        <div class="fr router">
          <router-view />
        </div>
      </div>
    </div>
    <div style="width:880px;margin:0 auto" v-show='this.$route.query.print'>
      <router-view />
    </div>

  </div>
</template>

<script>
import './assets/iconfont.css'
import headBar from './components/headBar.vue'
import sidebar from './components/sidebar.vue'
export default {
  name: 'App',
  data () {
    return {}
  },
  created () {
    // this.login()
  },
  methods: {
    // 登录
    login () {
      this.ajax(
        'http://transaction.test.myoilbank.com/getLoginUser.json',
        { url: location.origin },
        'get'
      ).then(res => {
        if (res.data.status === '0') {
          window.location.replace(location.origin + '/login.html')
        } else {
          this.$message(res.data.message)
        }
      })
    }
  },
  components: {
    headBar,
    sidebar
  }
}
</script>

<style lang="scss" scoped="" type="text/css">
.content {
  width: 1180px;
  margin: 0 auto;
  .sidebar {
    width: 280px;
    height: 1090px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.04);
    border: 1px solid rgba(234, 237, 243, 1);
  }
  .router {
    width: 880px;
  }
}
</style>



