<template>
  <div class="order">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>我的单证</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search">
      <el-form label-position="top"
               :model="search"
               class="clearfix">
        <el-form-item label="单证状态"
                      class="mr20">
          <el-select v-model="search.status"
                     placeholder="请输入或选择">
            <el-option label="未完成"
                       value="1"></el-option>
            <el-option label="已完成"
                       value="2"></el-option>
            <el-option label="全部"
                       value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="外贸合同号"
                      class="mr20 w340">
          <el-input v-model="search.purchaseContractNo"
                    placeholder="请输入或选择"
                    class="w340"></el-input>
        </el-form-item>
      </el-form>
      <el-button type="primary"
                 @click="query">查询</el-button>
    </div>
    <div class="detail"
         v-for="(item, key) in tableData"
         :key='key'>
      <h3>
        <p>
          <em>委托方:</em>
          <span>{{item.appointEnterpriseName}}</span>
        </p>
        <p>
          <em>外贸合同号:</em>
          <span>{{item.purchaseContractNo}}</span>
        </p>
        <ul class="fr">
          <router-link :to="{path:'/details',query: {blId: item.blId,sofId:item.sofId,id:item.appointId}}">
            <li>查看</li>
          </router-link>
          <router-link :to="{path:'/upload',query: {blId: item.blId,sofId:item.sofId,taskId:item.appointId}}">
            <li>上传</li>
          </router-link>
        </ul>
      </h3>
      <ul class="clearfix list">
        <li>
          <em>IMO:</em>
          <span>{{item.imo}}</span>
        </li>
        <li>
          <em>船名:</em>
          <span>{{item.vessel}}</span>
        </li>
        <li>
          <em>航次:</em>
          <span>{{item.voyage}}</span>
        </li>
        <li>
          <em>时间:</em>
          <span v-show='item.sofDate'>{{timestampToTime(item.sofDate)}}</span>
          <!-- <span>{{timestampToTime(item.sofDate)}}</span> -->
        </li>
        <li>
          <em>港口:</em>
          <span>{{item.port}}</span>
        </li>
        <li>
          <em>货物名称:</em>
          <span>{{item.goodsName}}</span>
        </li>
        <li>
          <em>吨数:</em>
          <span>{{item.metricTon}}</span>
        </li>
        <li>
          <em>SOF单证信息:</em>
          <span>{{item.sofStatus}}</span>
        </li>
        <li>
          <em>SOF附件:</em>
          <span>{{item.sofUrl}}</span>
        </li>
        <li>
          <em>B/L NO.:</em>
          <span>{{item.blNo}}</span>
        </li>
        <li>
          <em>提单信息:</em>
          <span>{{item.blStatus}}</span>
        </li>
        <li>
          <em>提单附件:</em>
          <span>{{item.blUrl}}</span>
        </li>
      </ul>
    </div>
    <el-pagination layout="prev, pager, next"
                   @current-change="handleCurrentChange"
                   :total="search.totalNum">
    </el-pagination>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'order',
  data () {
    return {
      search: {
        purchaseContractNo: '',
        status: '',
        pageNum: 1,
        totalNum: 0
      },
      tableData: []
    }
  },
  watch: {
  },
  created () {
    this.query()
  },
  methods: {
    timestampToTime (timestamp) {
      var date = new Date(timestamp)
      var Y = date.getFullYear() + '-'
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      var D = date.getDate() + ' '
      return Y + M + D
    },
    // 分页
    handleCurrentChange: function (e) {
      console.log(e)
      this.search.pageNum = e
      this.query()
    },
    // 数据展示
    query () {
      this.ajax(
        url.orderList,
        this.search,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          // console.log(res.data)
          this.tableData = res.data.data
          this.search.totalNum = res.data.total
        } else {
          this.$message(res.data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.mr20 {
  margin-right: 20px;
}
.w340 {
  width: 340px !important;
}
.el-date-editor.el-input {
  width: 160px;
}
.el-form-item {
  float: left;
  width: 160px;
}
.el-form-item__content {
  width: 160px;
}
.search {
  position: relative;
  .el-button--primary {
    position: absolute;
    top: 50px;
    left: 541px;
  }
}
.detail {
  border-left: 1px solid rgba(234, 237, 243, 1);
  border-right: 1px solid rgba(234, 237, 243, 1);
  border-bottom: 1px solid rgba(234, 237, 243, 1);
  h3 {
    padding: 10px 30px;
    border-top: 1px solid rgba(234, 237, 243, 1);
    border-bottom: 1px solid rgba(234, 237, 243, 1);
    p {
      display: inline;
      font-weight: 500;
      color: rgba(62, 63, 66, 1);
      line-height: 28px;
      padding-right: 40px;
    }
    li {
      float: left;
      width: 70px;
      height: 28px;
      line-height: 28px;
      border-radius: 4px;
      border: 1px solid rgba(226, 229, 237, 1);
      text-align: center;
      color: rgba(107, 108, 111, 1);
      font-weight: 400;
      cursor: pointer;
      margin-left: 20px;
    }
  }
  .list {
    padding: 20px 30px;
    li {
      float: left;
      width: 33%;
      padding-bottom: 10px;
      em {
        width: 40%;
        color: rgba(158, 160, 165, 1);
        float: left;
      }
      span {
        float: left;
        color: rgb(5, 7, 14);
      }
      a {
        color: #1665d8;
        padding-left: 20px;
      }
    }
  }
}
</style>



