<template>
  <div class="upload">
    <div class="tab">
      <span @click="back()">单证详情</span>>
      <span>单证上传</span>
    </div>
    <div class="item">
      <h3>提单</h3>
      <el-upload class="upload-demo"
                 drag
                 :action="actionUrl"
                 :on-remove="remove1"
                 :on-success='success1'>
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或
          <em>点击上传</em>
        </div>
        <div class="el-upload__tip"
             slot="tip">只能上传jpg/png/pdf文件，且不超过500kb</div>
      </el-upload>
    </div>
    <div class="item">
      <h3>SOF单证</h3>
      <el-upload class="upload-demo"
                 drag
                 :action="actionUrl"
                 :on-remove="remove2"
                 :on-success='success2'
                 >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或
          <em>点击上传</em>
        </div>
        <div class="el-upload__tip"
             slot="tip">只能上传jpg/png/pdf文件，且不超过500kb</div>
      </el-upload>
    </div>

  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'upload',
  data () {
    return {
      // actionUrl: url.actionUrl
      actionUrl: 'http://transaction.test.myoilbank.com/common/doc/upload.json'
    }
  },
  created () {
    console.log(this.actionUrl)
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    uploadFile (data, file) {
      this.ajax(
        url.upload,
        data,
        'post'
      ).then(res => {
        file.id = res.data.data.id
        if (res.data.status === 0) {
          this.$message(res.data.message)
        } else {
          this.$message(res.data.message)
        }
      })
    },
    deleteFile (data) {
      this.ajax(
        url.delete,
        data,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.$message(res.data.message)
        } else {
          this.$message(res.data.message)
        }
      })
    },
    remove1 (file, fileList) {
      let obj = {}
      obj.id = file.id
      obj.fileType = 'BL'
      this.deleteFile(obj)
    },
    remove2 (file, fileList) {
      let obj = {}
      obj.id = file.id
      obj.fileType = 'SOF'
      this.deleteFile(obj)
    },
    success1 (res, file) {
      let obj = {}
      obj = res.datas
      obj.id = this.$route.query.blId
      obj.taskId = this.$route.query.taskId
      obj.type = 'BL'
      this.uploadFile(obj, file)
    },
    success2 (res, file) {
      let obj = {}
      obj = res.datas
      obj.id = this.$route.query.sofId
      obj.taskId = this.$route.query.taskId
      obj.type = 'SOF'
      this.uploadFile(obj, file)
    }
  }
}
</script>

<style lang="scss" scoped>
.upload {
  h3 {
    height: 40px;
    background: rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(234, 237, 243, 1);
    box-shadow: 0px 1px 0px 0px rgba(234, 237, 243, 1);
    padding: 0 20px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(62, 63, 66, 1);
  }
  .item {
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.04);
    border: 1px solid rgba(234, 237, 243, 1);
    padding: 20px 0;
    margin-bottom: 20px;
  }
  .upload-demo {
    text-align: center;
    padding: 20px 0;
  }
}
</style>



