<template>
  <div class="details">
    <div class="info">
      <div class="clearfix con">
        <!-- <img :src="imgUrl"
             alt=""
             class="fl"> -->
        <div class="fl center">
          <!-- <h3>{{fullName}}</h3>
          <p>{{englishName}}</p> -->
          <h3>泉州市泉港兴通船务有限公司</h3>
          <p>Quanzhou Quangang Xingtong Shipping Co.,Ltd</p>
          <h3>装货时间事实记录</h3>
          <p> LAYTIME STATEMENT OF FACTS</p>
        </div>
      </div>
      <ul class="clearfix">
        <li>
          <em>船名 M/V:</em>
          <span>{{vessel}}</span>
        </li>
        <li>
          <em>航次 VOY.NO.:</em>
          <span>{{voyage}}</span>
        </li>
        <li>
          <em>装货名称 Loading:</em>
          <span>{{goodsName}}</span>
        </li>
        <li>
          <em>日期 DATE:</em>
          <span>{{sofDate}}</span>
        </li>
        <li>
          <em>吨数 MT:</em>
          <span>{{metricTon}}</span>
        </li>
        <li>
          <em>港口 LOADING PORT:</em>
          <span>{{port}}</span>
        </li>
      </ul>

    </div>
    <div class="form clearfix">
      <table border="1px"
             cellspacing="0px"
             style="border-collapse:collapse"
             class="table">
        <tr>
          <th width="15%">日期</th>
          <th width="15%">星期</th>
          <th width="15%">开始时间</th>
          <th width="15%">结束时间</th>
          <th width="40%">说明</th>
        </tr>
        <tr v-for="(item, key) in tableData"
            :key='key'>
          <td>
            {{timestampToTime(item.detailDate)}}
          </td>
          <td v-show="item.detailDate">
            {{format(new Date(item.detailDate).getDay())}}
          </td>
          <td v-show="!item.detailDate"></td>
          <td>
            {{item.beginTime}}
          </td>
          <td>
            {{item.endTime}}
          </td>
          <td>
            {{item.instruction}}
          </td>
        </tr>
      </table>
    </div>
    <ul class="remark clearfix">
      <li style="width:100%">
        <em>备注 REMARK:</em>
        <span>{{remark}}</span>
      </li>
      <li>
        <em>船舶登录总吨 SHIP'S.G.R.T:</em>
        <span>{{shipGrossWeight}}</span>
      </li>
      <li>
        <em>船舶净吨 SHIP'S.N.R.T:</em>
        <span>{{shipNetWeight}}</span>
      </li>
      <li>
        <em>船长 CAPTAIN:</em>
        <span></span>
      </li>
      <li>
        <em>代理公司 AS AGENT:</em>
        <span></span>
      </li>
    </ul>
  </div>
</template>

<script>
import url from '@/api/api'
import img from '@/assets/logo.jpg'
export default {
  name: 'printSof',
  data () {
    return {
      imgUrl: img,
      englishName: '',
      fullName: '',
      vessel: '',
      voyage: '',
      goodsName: '',
      sofDate: '',
      metricTon: '',
      port: '',
      remark: '',
      shipGrossWeight: '',
      shipNetWeight: '',
      activeName: 'first',
      fileList: [],
      tableData: [{
        detailDate: '',
        week: '',
        beginTime: '',
        endTime: '',
        instruction: ''
      }],
      ruleForm: {
        taskId: this.$route.query.id,
        blNo: '',
        consignor: '',
        vessel: '',
        loadingPort: '',
        captain: '',
        unloadingPort: '',
        consignee: '',
        notify: '',
        category: '',
        metricTon: '',
        longTon: '',
        bbls: '',
        ltrs: '',
        ltrsObsTrmp: '',
        remark: '',
        cpDate1: '',
        cpDate2: '',
        cpDate3: '',
        cpDate4: '',
        signNum: '',
        timeZone: '',
        timeDay: '',
        timeMonth: '',
        timeYear: '',
        masterName: ''
      }
    }
  },
  watch: {
  },
  created () {
    if (this.$route.query.sofId) {
      this.sofDetail()
    }
  },
  methods: {
    printing () {
      window.print()
    },
    format (e) {
      switch (e) {
        case 0:
          return 'Sun.'
        case 1:
          return 'Mon.'
        case 2:
          return 'Tues.'
        case 3:
          return 'Wed.'
        case 4:
          return 'Thur.'
        case 5:
          return 'Fri.'
        case 6:
          return 'Sat.'
        default:
          return ''
      }
    },
    timestampToTime (timestamp) {
      var date = new Date(timestamp)
      var Y = date.getFullYear() + '-'
      var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
      var D = date.getDate() + ' '
      return Y + M + D
    },
    // sof详情
    sofDetail () {
      this.ajax(
        url.sofDetail,
        { id: this.$route.query.sofId },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.fullName = res.data.data.fullName
          this.englishName = res.data.data.fullName
          // this.imgUrl = res.data.data.logo
          this.tableData = res.data.data.detailList
          this.vessel = res.data.data.vessel
          this.voyage = res.data.data.voyage
          this.goodsName = res.data.data.goodsName
          this.sofDate = this.timestampToTime(res.data.data.sofDate)
          this.metricTon = res.data.data.metricTon
          this.port = res.data.data.port
          this.remark = res.data.data.remark
          this.shipGrossWeight = res.data.data.shipGrossWeight
          this.shipNetWeight = res.data.data.shipNetWeight
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // bill详情
    billDetail () {
      this.ajax(
        url.billDetail,
        { id: this.$route.query.blId },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.ruleForm = res.data.data
        } else {
          this.$message(res.data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.remark {
  padding-bottom: 30px;
  li {
    float: left;
    width: 50%;
    padding: 10px 0;
    em {
      padding-right: 10px;
      color: #9ea0a5;
    }
  }
}
.details {
  .file {
    border: 1px solid rgba(234, 237, 243, 1);
    h3 {
      padding: 10px;
      border-bottom: 1px solid rgba(234, 237, 243, 1);
      em {
        line-height: 33px;
        color: #3e3f42;
        font-weight: bold;
      }
      span {
        color: #3e3f42;
        font-weight: normal;
        padding: 6px;
        background: linear-gradient(
          360deg,
          rgba(246, 247, 249, 1) 0%,
          rgba(255, 255, 255, 1) 100%
        );
        box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
          0px 2px 0px 0px rgba(255, 255, 255, 0.05);
        border-radius: 4px;
        border: 1px solid rgba(216, 220, 230, 1);
      }
    }
  }
  .tag {
    padding: 20px 0;
    background: #fbfbfd;
    div {
      float: left;
    }
    .el-tag {
      box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
        0px 2px 0px 0px rgba(255, 255, 255, 0.05);
      border-radius: 4px;
      border: 1px solid rgba(216, 220, 230, 1);
      width: 104px;
      height: 44px;
      line-height: 44px;
      text-align: center;
      margin-right: 20px;
      color: rgba(62, 63, 66, 1);
      i {
        color: rgba(216, 220, 230, 1);
        padding-right: 6px;
      }
    }
    .el-tag:hover {
      background: linear-gradient(
        360deg,
        rgba(232, 233, 235, 1) 0%,
        rgba(239, 240, 242, 1) 100%
      );
    }
  }
  .info {
    .center {
      width: 100%;
      text-align: center;
    }
    .con {
      padding: 30px 0;
      img {
        width: 70px;
        padding-right: 30px;
      }
      h3 {
        height: 26px;
        font-size: 20px;
        color: rgba(51, 51, 51, 1);
        line-height: 26px;
        padding: 20px 0;
      }
      p {
        height: 19px;
        font-size: 14px;
        color: rgba(51, 51, 51, 1);
        line-height: 19px;
      }
    }
    h4 {
      height: 28px;
      font-size: 14px;
      font-weight: 500;
      color: rgba(62, 63, 66, 1);
      line-height: 28px;
    }
    li {
      float: left;
      width: 33%;
      padding: 10px 0;
      em {
        padding-right: 10px;
        color: #9ea0a5;
      }
    }
  }
  .form {
    border: 1px solid #eaedf3;
    padding: 20px;
    margin-bottom: 20px;
  }
  .table {
    text-align: center;
    margin: 20px 0;
    width: 100%;
    th {
      border: 1px solid #eaedf3;
      line-height: 40px;
      background: #f5f6f7;
    }
    td {
      border: 1px solid #eaedf3;
      line-height: 50px;
    }
  }
  .table1 {
    margin: 20px 0;
    width: 100%;
    font-size: 12px;
    th {
      border: 1px solid #eaedf3;
      text-align: left;
      line-height: 40px;
      padding: 20px 0 20px 20px;
      em {
        padding-right: 10px;
      }
    }
    td {
      border: 1px solid #eaedf3;
      width: 16%;
      line-height: 30px;
    }
  }
  .bg {
    background: #f5f6f7;
    padding: 4px;
    color: #333333;
  }
}
</style>



