<template>
  <div class="header">
    <div class="w1180 clearfix">
      <div class="fl">
        <i class="iconfont icon-chuan"></i>
        <span>{{system}}</span>
      </div>
      <div class="fr">
        <span>{{companyName}}</span>
        <span @click="loginOut()">Sign Out</span>
      </div>
    </div>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'headerBar',
  data () {
    return {
      system: '船代业务系统',
      companyName: ''
    }
  },
  created () {
    this.userInfor()
  },
  methods: {
    loginOut () {
      window.location.replace(url.loginOut)
    },
    userInfor () {
      this.ajax(
        url.userInfor,
        {},
        'get'
      ).then(res => {
        this.companyName = res.data.enterpriseName
      })
    }
  }

}
</script>

<style lang="scss"  scoped>
.header {
  height: 70px;
  background: #3e3f42;
  line-height: 70px;
  margin-bottom: 20px;
  .icon-chakan2 {
    color: #ffffff;
  }
  .fl {
    span {
      color: #ffffff;
      font-size: 20px;
      padding-left: 10px;
    }
  }
  .fr {
    color: #eaedf3;
    font-size: 14px;
    span {
      padding-right: 20px;
    }
  }
}
</style>



