<template>
  <div class="sof"
       @click="hide">
    <div class="tab">
      <span @click="back()">任务详情</span>>
      <span v-show='!this.$route.query.sofId'>新建SOF</span>
      <span v-show='this.$route.query.sofId'>修改SOF</span>
    </div>
    <div class="basic">
      <h3>任务概况</h3>
      <ul class="clearfix">
        <li>
          <em>委托公司 :</em>
          <span>{{taskProfile.nomineeCompany }}</span>
        </li>
        <li>
          <em>外贸合同号 :</em>
          <span>{{taskProfile.purchaseContractNo}}</span>
        </li>
        <li>
          <em>数量 :</em>
          <span>{{taskProfile.quantity}}</span>
        </li>
        <li>
          <em>油种 :</em>
          <span>{{taskProfile.category}}</span>
        </li>
        <li>
          <em>规格 :</em>
          <span>{{taskProfile.specification}}</span>
        </li>
        <li>
          <em>装期 :</em>
          <span>{{taskProfile.laycan}}</span>
        </li>
      </ul>
    </div>
    <div class="tag clearfix">
      <div @click="storage()"
           v-show="zancun">
        <el-tag>
          <i class="iconfont icon-security"></i>暂存</el-tag>
      </div>
      <router-link target="_blank"
                   :to="{path:'/printSof',query: {sofId: this.$route.query.sofId,id:this.$route.query.id,print:1}}">
        <div>
          <el-tag>
            <i class="iconfont icon-security"></i> 打印单证</el-tag>
        </div>
      </router-link>
      <div @click="submitForm('ruleForm')">
        <el-tag>
          <i class="iconfont icon-security"></i>确认提交</el-tag>
      </div>
      <router-link :to="{path:'/upload',query: {blId: this.$route.query.blId,sofId:this.$route.query.sofId,taskId:this.$route.query.id}}">
        <div>
          <el-tag>
            <i class="iconfont icon-security"></i> 上传单证</el-tag>
        </div>
      </router-link>
    </div>
    <div class="form clearfix">
      <el-form :model="ruleForm"
               label-position="top"
               :rules="rules"
               ref="ruleForm"
               class="clearfix">
        <el-form-item label="船名M/V"
                      prop="vessel">
          <el-input v-model="ruleForm.vessel"></el-input>
        </el-form-item>
        <el-form-item label="航次VOY.NO"
                      prop="voyage"
                      class="w160">
          <el-input v-model="ruleForm.voyage"></el-input>
        </el-form-item>
        <el-form-item label="IMO"
                      prop="imo">
          <el-input v-model="ruleForm.imo"></el-input>
        </el-form-item>
        <el-form-item label="装货名称LOADING"
                      prop="goodsName">
          <el-input v-model="ruleForm.goodsName"></el-input>
        </el-form-item>
        <el-form-item label="吨数MT"
                      prop="metricTon"
                      class="w160">
          <el-input v-model="ruleForm.metricTon"></el-input>
        </el-form-item>
        <el-form-item label="联系人"
                      prop="contactPerson">
          <el-input v-model="ruleForm.contactPerson"></el-input>
        </el-form-item>
        <el-form-item label="日期DATE"
                      required>
          <el-col :span="1">
            <el-form-item prop="sofDate">
              <el-date-picker type="date"
                              placeholder="选择日期"
                              v-model="ruleForm.sofDate"
                              style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="港口LOADING PORT"
                      prop="port"
                      class="w160">
          <el-input v-model="ruleForm.port"></el-input>
        </el-form-item>
        <el-form-item label="联系电话"
                      prop="contactNumber">
          <el-input v-model="ruleForm.contactNumber"></el-input>
        </el-form-item>

        <table border="1px"
               cellspacing="0px"
               style="border-collapse:collapse"
               class="table">
          <tr>
            <th>日期</th>
            <th width=70>星期</th>
            <th>开始时间</th>
            <th>结束时间</th>
            <th>说明</th>
          </tr>
          <tr v-for="(item, key) in ruleForm.detailList"
              :key='key'
              @contextmenu.prevent='rightKey(e,key)'>
            <td>
              <el-date-picker v-model="item.detailDate"
                              type="date"
                              placeholder="选择日期">
              </el-date-picker>
            </td>
            <td v-show="item.detailDate">
              {{format(new Date(item.detailDate).getDay())}}
            </td>
            <td v-show="!item.detailDate"></td>
            <td class="qq">
              <el-time-select placeholder="起始时间"
                              v-model="item.beginTime"
                              :picker-options="{
                              start: '00:00',
                              step: '00:15',
                              end: '24:00'
                            }">
              </el-time-select>
            </td>
            <td class="qq">
              <el-time-select placeholder="结束时间"
                              v-model="item.endTime"
                              :picker-options="{
                            start: '00:00',
                            step: '00:15',
                            end: '24:00',
                            minTime: item.beginTime
                          }">
              </el-time-select>
            </td>
            <td style='position: relative'>
              <el-autocomplete class="inline-input"
                               v-model="item.instruction"
                               :fetch-suggestions="querySearch"
                               placeholder="请输入内容"
                               @focus='keynum(key)'
                               @select="handleSelect">
                <template slot-scope="{ item }">
                  <div class="name">{{ item.introEn }}</div>
                </template>
              </el-autocomplete>
            </td>
          </tr>
        </table>

        <el-form-item label="备注 REMARK"
                      prop="remark"
                      class="w100">
          <el-input v-model="ruleForm.remark"></el-input>
        </el-form-item>
        <el-form-item label="船舶登录总吨 SHIP'S.G.R.T"
                      prop="shipGrossWeight"
                      class="fl">
          <el-input v-model="ruleForm.shipGrossWeight"></el-input>
        </el-form-item>
        <el-form-item label="船舶净吨 SHIP'S.N.R.T"
                      prop="shipNetWeight"
                      class="fr">
          <el-input v-model="ruleForm.shipNetWeight"></el-input>
        </el-form-item>
      </el-form>
      <div class="sign clearfix">
        <p class="fl">
          船长CAPTAIN
        </p>
        <p class="fr">
          代理公司 AS AGENT
        </p>
      </div>
    </div>

    <ul class="menu"
        id="menu"
        v-show="show">
      <li @click="upInsert">
        <i class="iconfont icon-doc"></i> 上方插入行</li>
      <li @click="downInsert">
        <i class="iconfont icon-doc"></i>下方插入行</li>
      <li @click="del">
        <i class="iconfont icon-doc"></i>删除行</li>
    </ul>
  </div>
</template>

<script>
import url from '@/api/api'
export default {
  name: 'sof',
  data () {
    return {
      num: '',
      template: [],
      zancun: true,
      show: false,
      index: '',
      taskProfile: {},
      ruleForm: {
        taskId: this.$route.query.id,
        vessel: '',
        voyage: '',
        imo: '',
        goodsName: '',
        metricTon: '',
        contactPerson: '',
        sofDate: '',
        port: '',
        contactNumber: '',
        remark: '',
        shipGrossWeight: '',
        shipNetWeight: '',
        detailList: [{
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }, {
          detailDate: '',
          week: '',
          beginTime: '',
          endTime: '',
          instruction: ''
        }]
      },
      rules: {
        vessel: [
          { required: true, message: '请输入船名', trigger: 'blur' }
        ],
        voyage: [
          { required: true, message: '请输入航次', trigger: 'blur' }
        ],
        goodsName: [
          { required: true, message: '请输入装货名称', trigger: 'blur' }
        ],
        metricTon: [
          { required: true, message: '请输入吨数', trigger: 'blur' }
        ],
        sofDate: [
          { type: 'date', required: true, message: '请选择日期', trigger: 'blur' }
        ],
        port: [
          { required: true, message: '请输入港口', trigger: 'blur' }
        ],
        contactNumber: [
          { required: true, message: '请输入电话', trigger: 'blur' }
        ],
        shipGrossWeight: [
          { required: true, message: '请输入总吨数', trigger: 'blur' }
        ],
        shipNetWeight: [
          { required: true, message: '请输入净吨数', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {
  },
  created () {
    this.sql()
    this.profile()
    if (this.$route.query.sofId) {
      //   this.zancun = false
      this.sofDetail()
    }
  },
  methods: {
    querySearch (queryString, cb) {
      var restaurants = this.template
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter (queryString) {
      return (restaurant) => {
        return (restaurant.introEn.toLowerCase().indexOf(queryString.toLowerCase()) === 0)
      }
    },
    handleSelect (item) {
      this.ruleForm.detailList[this.num].instruction = item.introEn
    },
    keynum (e) {
      this.num = e
    },
    sql () {
      this.ajax(
        url.sql,
        { introEn: '' },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          console.log(res.data.data)
          this.template = res.data.data
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // 任务概况
    profile () {
      this.ajax(
        url.taskProfile,
        { taskId: this.$route.query.id },
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.taskProfile = res.data.data
        } else {
          this.$message(res.data.message)
        }
      })
    },
    back () {
      this.$router.go(-1)
    },
    // 验证
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submit()
        } else {
          return false
        }
      })
    },
    // 格式化星期
    format (e) {
      switch (e) {
        case 0:
          return 'Sun.'
        case 1:
          return 'Mon.'
        case 2:
          return 'Tues.'
        case 3:
          return 'Wed.'
        case 4:
          return 'Thur.'
        case 5:
          return 'Fri.'
        case 6:
          return 'Sat.'
        default:
          return ''
      }
    },
    // 右键功能
    rightKey (e, key) {
      e = e || event
      // console.log(e.screenX + '|' + e.screenY)
      document.getElementById('menu').style.top = e.screenY + 460 + 'px'
      document.getElementById('menu').style.left = e.screenX + 'px'
      // console.log(key)
      this.index = key
      this.show = true
    },
    // 向上插入
    upInsert () {
      let obj = {
        detailDate: '',
        week: '',
        beginTime: '',
        endTime: '',
        instruction: ''
      }
      // console.log(this.ruleForm.detailList)
      this.ruleForm.detailList.splice(this.index, 0, obj)
    },
    // 向下插入
    downInsert () {
      let obj = {
        detailDate: '',
        week: '',
        beginTime: '',
        endTime: '',
        instruction: ''
      }
      // console.log(this.ruleForm.detailList)
      this.ruleForm.detailList.splice(this.index + 1, 0, obj)
    },
    // 右键删除
    del () {
      if (this.ruleForm.detailList.length > 1) {
        this.ruleForm.detailList.splice(this.index, 1)
        // console.log(this.ruleForm.detailList)
      } else {
        this.$message('不能删除')
      }
    },
    hide () {
      this.show = false
    },
    // 提交
    submit () {
      this.ajax(
        url.sofSubmit,
        this.ruleForm,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.$message('成功')
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // 暂存
    storage () {
      this.ajax(
        url.sofStorage,
        this.ruleForm,
        'post'
      ).then(res => {
        if (res.data.status === 0) {
          this.$message('成功')
        } else {
          this.$message(res.data.message)
        }
      })
    },
    // sof详情
    sofDetail () {
      this.ajax(
        url.sofDetail,
        { id: this.$route.query.sofId },
        'post'
      ).then(res => {
        // console.log(res.data.data)
        if (res.data.status === 0) {
          this.ruleForm = res.data.data
          if (res.data.data.status === '2') {
            this.zancun = false
          }
        } else {
          this.$message(res.data.message)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.w100 {
  width: 100% !important;
}
.w160 {
  width: 160px !important;
  margin: 0 20px;
}
.el-textarea {
  margin: 20px 0;
}
.sof {
  .basic {
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.04);
    border: 1px solid rgba(234, 237, 243, 1);
    padding: 30px;
    h3 {
      font-size: 20px;
      font-weight: 500;
      color: rgba(62, 63, 66, 1);
    }
    li {
      float: left;
      width: 33%;
      padding-top: 15px;
      em {
        width: 80px;
        float: left;
        text-align: right;
        padding-right: 10px;
        color: #666666;
      }
      span {
        float: left;
        color: #333333;
      }
    }
  }
  .tag {
    padding: 20px 0;
    background: #fbfbfd;
    div {
      float: left;
      span{
       cursor:pointer;
      }
    }
    .el-tag {
      box-shadow: 0px 1px 1px 0px rgba(22, 29, 37, 0.05),
        0px 2px 0px 0px rgba(255, 255, 255, 0.05);
      border-radius: 4px;
      border: 1px solid rgba(216, 220, 230, 1);
      width: 104px;
      height: 44px;
      line-height: 44px;
      text-align: center;
      margin-right: 20px;
      color: rgba(62, 63, 66, 1);
      i {
        color: rgba(216, 220, 230, 1);
        padding-right: 6px;
      }
    }
    .el-tag:hover {
      background: linear-gradient(
        360deg,
        rgba(232, 233, 235, 1) 0%,
        rgba(239, 240, 242, 1) 100%
      );
    }
  }
  .form {
    border: 1px solid #eaedf3;
    padding: 20px;
    margin-bottom: 20px;
    .el-form-item {
      width: 318px;
      float: left;
      display: block;
    }
  }
  .table {
    text-align: center;
    margin: 20px 0;
    width: 100%;
    th {
      border: 1px solid #eaedf3;
      line-height: 40px;
      background: #f5f6f7;
    }
    td {
      border: 1px solid #eaedf3;
      line-height: 50px;
      .el-date-editor {
        width: 148px;
      }
    }
  }
  .menu {
    position: absolute;
    width: 141px;
    height: 132px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.3);
    li {
      padding: 12px 20px;
      i {
        padding-right: 10px;
      }
    }
    li:hover {
      background: #f5f6f7;
    }
  }
  .sign {
    margin-bottom: 40px;
    p {
      width: 320px;
      height: 60px;
      border-bottom: 2px solid #9ea0a5;
    }
  }
}
.qq {
  width: 120px !important;
  .el-date-editor {
    width: 120px !important;
  }
}
.el-autocomplete {
  width: 380px !important;
}
</style>



