const protocol = location.protocol // http:
const siteList = ['']
const urlist = location.hostname.split('.') // ["localhost"]
// console.log(urlist)
// 区分开发和生产环境
function checkEvn () {
  if (urlist.includes('localhost')) {
    return setUrl('10.26.15.210:8080/shipagent/')
  }
  if (urlist.includes('test')) {
    return setUrl('test.shipagent.myoilbank.com/')
  }
  if (urlist[0].indexOf('uat') > -1) {
    return setUrl('uat.shipagent.myoilbank.com/')
  }
  return setUrl('.shipagent.myoilbank.com/')
}
// 拼接API地址
function setUrl (string) {
  let url = []
  siteList.forEach(e => {
    let ne = [protocol, '//', string, e].join('')
    url.push(ne)
  })
  // console.log(url)
  return url
}
const siteUrl = checkEvn()

export default siteUrl



